@charset "UTF-8";
/** 基准颜色定义 **/
/** 主题色 **/
/** 基准尺寸 **/
/** zIndex **/
/** 组件 **/
body {
  line-height: 1.5;
  overflow: hidden;
  color: #333;
  font-size: 16px;
  background: #f8f8f8; }

a {
  color: #266cef; }

#header {
  position: relative;
  display: flex;
  align-items: center;
  height: 3.25rem;
  background: #2f3640; }
  #header .icon-button {
    border-width: 0.725rem; }
  #header .btn-menu {
    margin-left: 10px; }
  #header .btn-close {
    display: none;
    color: #fff; }

.app-title {
  display: flex;
  align-items: center;
  width: 246px;
  height: 100%;
  overflow: hidden;
  font-size: 1.5rem;
  color: #fff;
  background: #21262d;
  transition: all .2s; }
  .app-title > span {
    flex: 1;
    max-width: calc(100% - 42px);
    padding-left: 20px;
    box-sizing: border-box;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }

#main {
  position: relative;
  display: flex;
  height: calc(100vh - 3.25rem); }

.aside {
  width: 246px;
  height: 100%;
  overflow: hidden;
  background: #21262d;
  transition: all .2s; }

.list {
  height: calc(100% - 58px);
  padding: 0 10px;
  overflow: auto;
  list-style: none; }
  .list a {
    display: block;
    height: 32px;
    margin-bottom: 5px;
    line-height: 32px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: .95rem;
    padding-left: calc(1em + 7px);
    color: #fff;
    background: url("./icon/marker.png") left center no-repeat;
    background-size: 1em auto;
    opacity: .7; }
    .list a:hover, .list a.is-active {
      color: #fcb324;
      opacity: 1; }
    .list a.is-active {
      box-shadow: 0 1px 0; }

.map-container {
  position: relative;
  flex: 1;
  height: 100%; }

.info-title {
  padding-bottom: 5px;
  margin-bottom: 5px;
  font-size: 1.1rem;
  border-bottom: 1px dotted #ababab; }

.info-body {
  display: flex; }

.info-content {
  flex: 1;
  padding-right: 10px;
  line-height: 1.8; }

.info-photo {
  height: 90px;
  box-sizing: border-box;
  overflow: hidden;
  background: #dedede url("./icon/loading.svg") center no-repeat; }
  .info-photo > img {
    width: 120px; }

.sources-tip {
  position: absolute;
  left: 0;
  bottom: 30px;
  z-index: 1;
  padding: 0 .5em;
  font-size: 12px;
  background: rgba(255, 255, 255, 0.8); }
  .sources-tip img {
    vertical-align: middle; }

.is-aside-mini .app-title,
.is-aside-mini .aside {
  width: 0;
  padding: 0; }

@media (max-width: 480px) {
  body {
    font-size: 14px; }
  .app-title,
  .aside {
    position: absolute;
    z-index: 5;
    width: 246px !important; }
  #header .btn-close {
    display: block; }
  .info-body {
    flex-direction: column; }
  .info-photo {
    height: 120px; }
    .info-photo > img {
      width: 100%; }
  .is-aside-mini .app-title,
  .is-aside-mini .aside {
    transform: translateX(-100%); } }

+ .form-group {
  margin-top: 10px; }

.form-control {
  width: 100%;
  padding: .4em .6em;
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #c9c9c3;
  border-radius: 3px; }

.input-group {
  display: flex;
  padding: 10px; }
  .input-group > .form-control {
    flex: 1;
    padding-top: 0;
    padding-bottom: 0;
    border-radius: 0; }
    .input-group > .form-control:first-child:not(:last-child) {
      border-radius: 3px 0 0 3px; }
  .input-group > button {
    border-radius: 0; }
    .input-group > button:last-child {
      border-radius: 0 3px 3px 0; }

.text-primary {
  color: #fcb324; }

.text-info {
  color: #266cef; }

.text-success {
  color: #58cc67; }

.text-warning {
  color: #fcb324; }

.text-danger {
  color: #e66963; }

.text-muted {
  color: #ababab; }

.text-center {
  text-align: center; }
