.notify {
  position: absolute;
  padding: 10px 20px;
  line-height: 1;
  white-space: nowrap;
  visibility: visible;
  opacity: 1;
  -webkit-transition: all .3s;
  transition: all .3s; }
  .notify.invisible {
    visibility: hidden;
    opacity: 0; }
  .notify.pos-top-left {
    top: 20px;
    left: 20px; }
  .notify.pos-top-center {
    top: 20px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); }
  .notify.pos-top-right {
    top: 20px;
    right: 20px; }
  .notify.pos-center {
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  .notify.pos-bottom-left {
    bottom: 20px;
    left: 20px; }
  .notify.pos-bottom-center {
    bottom: 20px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); }
  .notify.pos-bottom-right {
    bottom: 20px;
    right: 20px; }
  .notify.darken {
    background: rgba(0, 0, 0, 0.8); }
  .notify.lighten {
    background: rgba(255, 255, 255, 0.8); }
  .notify::before {
    content: "";
    display: inline-block;
    width: 1em;
    height: 1em;
    margin-right: .5em;
    vertical-align: -0.15em;
    background: url(about:blank) center no-repeat;
    background-size: contain; }
  .notify.is-loading {
    z-index: 20;
