    .notify.is-loading::before {
      background-image: url(/static/media/loading.cb302751.svg); }
  .notify.is-notice {
    z-index: 10;
    color: #fcb324; }
    .notify.is-notice::before {
      background-image: url(/static/media/warning.14d5cea5.svg); }

